}
.Navbar_menu__3Hmg7 a::before{
    content: "";
    position: absolute;
    width: 100%;
    height: 1.5px;
    bottom: 0;
    left: 0;
    background-color:  #5b341c;
    visibility: hidden;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition: all 0.4s ease-in-out;
}
.Navbar_menu__3Hmg7 a:hover::before{
    visibility: visible;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}

