footer{
    width: 100%;
    background-color:#a46c4c;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 1rem;
    height: 100px;
    grid-area: "footer";
  }
  footer ul{
    list-style: none;
  }
  footer h3{
    font-size: 1rem;
    text-align: center;
  }
  footer section{
    margin-top: 1rem;
  }
footer section:nth-child(2){
  margin-top: 50px;
}

.sectionRedes{
  display: flex;
  flex-direction: column;
  margin: 1rem;
  font-size: 0.5rem;
}
.sectionRedes a{
  text-decoration: none;
}
.sectionRedes div:nth-child(2){
  display: flex;
  gap:1rem;
  width: 8px;
}
.sectionRedes svg{
  font-size: 1.3rem;
}
.facebook{
  background-color: #000;
  padding: 8px;
  display: flex;
  width: fit-content;
  border-radius: 100%;
}
.facebook svg{
  text-decoration: none;
  color:#3b5998;

}
.instagram{
  background-color: #000;
  padding: 8px;
  display: flex;
  width: fit-content;
  border-radius: 100%;
}
.instagram svg{
  text-decoration: none;
  color: #C13584;

}
